import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	data() {
		return {
			userName: '',
			passWord: ''
		};
	},
	methods: {
		submitEvent() {
			if(this.userName=='' || this.passWord=='') {
				this.$refs.popup.open();
				return 
			}
			console.log(this.userName)
			console.log(this.passWord)
			uni.redirectTo({
				url: '../index/index'
			});
		},
		closePopup(){
			this.$refs.popup.close();
		}
	},
	components: {uniPopup}
}
